import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Container,
  Card,
  CardBody,
  CardHeader,
  Table,
  Row,
  Col,
  Button,
  Collapse,
  Badge,
} from "reactstrap";
import Loading from "../../../components/Loading";
import { clientGetInvoice } from "../../../redux/actions/clientInvoiceAction";
import { getDateFormat, fixNumber } from "helpers/UtilsHelper";
import "./Billing.scss";

import InvoiceDetailModal from "./InvoiceDetailModal.jsx";
import PaymentModal from "./PaymentModal";
import { useState } from "react";
import { useEffect } from "react";

export default function Billing(props) {
  const dispatch = useDispatch();
  const invoiceData = useSelector((state) => state.clientInvoice.data);
  const loading = useSelector((state) => state.clientInvoice.loading);
  const { invoiceDetail } = props;
  const listInvoice = invoiceData.result;
  const [isOpenAccountActivityToggle, setIsOpenAccountActivityToggle] =
    useState(true);
  const toggleAccountActivity = () => {
    setIsOpenAccountActivityToggle(!isOpenAccountActivityToggle);
  };
  const [isExportPDF, setIsExportPDF] = useState(false);
  const [isAddPayment, setIsAddPayment] = useState(false);
  const [invoiceId, setInvoiceId] = useState("");
  const [paymentData, setPaymentData] = useState({});
  const [ordering] = useState("updated_at");
  const [desc] = useState(false);

  useEffect(() => {
    dispatch(clientGetInvoice({ ordering, desc }));
  }, [ordering, desc]);

  const countTotalBalance = (listInvoice) => {
    if (listInvoice && listInvoice.length > 0) {
      const totalChargedBooking = listInvoice.map((item) => {
        return Number(item.priceRemain);
      });
      const totalCharged =
        totalChargedBooking &&
        totalChargedBooking.reduce(
          (total, currentValue) => total + currentValue
        );
      return totalCharged;
    } else {
      return 0;
    }
  };

  const toggleExportPDF = (id) => {
    setIsExportPDF(!isExportPDF);
    setInvoiceId(id);
  };

  const togglePaymentModal = (data) => {
    setIsAddPayment(!isAddPayment);
    setPaymentData(data);
  };

  const totalBalance = countTotalBalance(listInvoice);
  return (
    <Container fluid className="client-billing">
      {isExportPDF && (
        <InvoiceDetailModal
          size="lg"
          isOpen={isExportPDF}
          toggle={toggleExportPDF}
          invoice={invoiceDetail}
          invoiceId={invoiceId}
        />
      )}
      {isAddPayment && (
        <PaymentModal
          className="modal-dialog-centered"
          isOpen={isAddPayment}
          toggle={togglePaymentModal}
          data={paymentData}
        />
      )}

      <Card className="mx-auto">
        <CardHeader className="text-center border-bottom mt-2">
          <h2 className="mb-3 text-uppercase">Billing</h2>
        </CardHeader>
        <CardBody>
          <Row>
            <Col md={12} className="ml-3 mr-3 mb-5 border-right">
              <Row>
                <h3 className="text-primary">CURRENT INVOICES</h3>
              </Row>
              <Row className="border-bottom">
                <Table className="current-invoices" responsive>
                  <thead className="thead-light">
                    <tr></tr>
                  </thead>
                  <tbody>
                    {listInvoice &&
                      listInvoice.map((item) => {
                        const dateFormat = getDateFormat(item.createdAt);
                        return (
                          <tr
                            key={item.id}
                            className={`${
                              Number(item.priceRemain) > 0 ? "" : "d-none"
                            }`}
                          >
                            <td>
                              <Badge color="info" pill>
                                <span className="status">DUE</span>
                              </Badge>
                            </td>
                            <td> {dateFormat} </td>
                            <td className="cursor-pointer">
                              <Button
                                color="primary"
                                onClick={() => togglePaymentModal(item)}
                              >
                                Pay Now
                              </Button>
                            </td>
                            <td className="text-center">
                              ${fixNumber(item.priceRemain)}
                            </td>
                          </tr>
                        );
                      })}
                  </tbody>
                </Table>
              </Row>
            </Col>
          </Row>
          <Row>
            <Col md={12} className="ml-3 mr-3 border-right">
              <Row>
                <h3 className="text-primary">ACCOUNT HISTORY</h3>
              </Row>
              <Row onClick={toggleAccountActivity} className="cursor-pointer">
                <div>
                  <Button color="link">
                    {!isOpenAccountActivityToggle ? (
                      <i className="fas fa-chevron-down fa-1x" />
                    ) : (
                      <i className="fas fa-chevron-up fa-1x" />
                    )}
                  </Button>
                </div>
                <Col>
                  <h3
                    className="mt-2 text-info"
                    onClick={toggleAccountActivity}
                  >
                    ACCOUNT ACTIVITY
                  </h3>
                </Col>
                <Col className="text-right">
                  {/* <span className="text-success">
                    ({listInvoice && listInvoice.length} item)
                  </span> */}
                  <i className="far fa-calendar-alt ml-2"></i>
                </Col>
              </Row>
              <Row className="d-block mr-0">
                <Collapse isOpen={isOpenAccountActivityToggle}>
                  {loading && <Loading />}
                  <Table className="account-history" responsive>
                    <thead className="thead-light">
                      <tr>
                        <th colSpan="3" />
                        {/* <th> Charges </th> */}
                        <th> Payments </th>
                        {/* <th> Balance </th> */}
                        <th colSpan="2"> Download </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td></td>
                        <td colSpan="5">Balance</td>
                        {/* <td colSpan="2">
                          {fixNumber(start_balance)}
                          N/A
                        </td> */}
                      </tr>
                      {listInvoice &&
                        listInvoice.map((item) => {
                          const date = getDateFormat(
                            item.createdAt,
                            "MM/DD/YYYY"
                          );
                          const date2 = getDateFormat(item.createdAt);

                          const charges = item.servicePrice;
                          const balance = item.priceRemain;
                          const payment = Number(charges) - Number(balance);
                          return (
                            <tr key={item.id}>
                              <td>{date}</td>
                              <td colSpan="2">
                                <div>Invoice #{item.invoiceNumber}</div>
                                <div className="client-billing__limit-div">
                                  -{date2} {item.appointment.serviceType} $
                                  {fixNumber(item.appointment.servicePrice)}
                                </div>
                              </td>
                              {/* <td>${fixNumber(charges)}</td> */}
                              <td colSpan="2">${fixNumber(payment)}</td>
                              {/* <td>${fixNumber(balance)}</td> */}
                              <td>
                                <button
                                  className="btn btn-link download-invoice"
                                  onClick={() => toggleExportPDF(item.id)}
                                >
                                  <span className="fas fa-download cursor-pointer"></span>
                                </button>
                              </td>
                            </tr>
                          );
                        })}
                      <tr>
                        <td></td>
                        <td colSpan="3">Ending Balance</td>
                        <td colSpan="2">${fixNumber(totalBalance)}</td>
                      </tr>
                    </tbody>
                  </Table>
                </Collapse>
              </Row>
            </Col>
          </Row>
        </CardBody>
      </Card>
    </Container>
  );
}
