import React from "react";
import _ from "lodash";
import { Container, CardFooter, Table, Button, Modal } from "reactstrap";
import {
  getStatusInfo,
  getDateFormat,
  getUserFullName,
} from "helpers/UtilsHelper";
import { exportClientInvoicePDF } from "helpers/ExportHelper";
import invoiceApi from "../../../api/invoiceApi";
import cons from "../../../constants";
import "./InvoiceDetail.scss";
import { useState } from "react";
import { useEffect } from "react";

export default function InvoiceDetail(props) {
  const { invoiceId, isOpen, toggle } = props;
  const [status, setStatus] = useState("");
  const [servicePrice, setServicePrice] = useState(0);
  const [createdAt, setCreatedAt] = useState("");
  const [invoiceNumber, setInvoiceNumber] = useState("");
  const [appointment, setAppointment] = useState({});
  const [provider, setProvider] = useState({});

  useEffect(() => {
    if (invoiceId) {
      invoiceApi
        .invoiceDetail(invoiceId)
        .then((res) => {
          setStatus(res.status);
          setServicePrice(res.servicePrice);
          setCreatedAt(res.createdAt);
          setInvoiceNumber(res.invoiceNumber);
          setAppointment(res.appointment);
          setProvider(res.provider);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, []);

  const getInfo = (data) => {
    if (data) {
      const phoneNumber = data.phoneNumber;
      const email = data.email;
      const address = data.address;
      return {
        phoneNumber,
        email,
        address,
      };
    } else {
      const phoneNumber = "Unknown";
      const email = "Unknown";
      const address = "Unknown";
      return {
        phoneNumber,
        email,
        address,
      };
    }
  };

  const exportPDF = () => {
    exportClientInvoicePDF("invoice-table", "total-invoice-table");
  };

  const statusInfo = getStatusInfo(cons.INVOICE_STATUS_INFO, status);
  const clientFullName = getUserFullName(appointment.client);
  const providerFullName = getUserFullName(provider);
  const description = appointment.serviceType || "N/A";
  const clientInfo =
    _.isNull(appointment) && _.isNull(appointment.client)
      ? ""
      : getInfo(appointment.client);
  const providerInfo = _.isNull(provider) ? "" : getInfo(provider);

  return (
    <Modal size="lg" isOpen={isOpen} toggle={toggle}>
      <Container className="invoice-detail">
        <div className="text-right">
          <Button color="link" onClick={toggle}>
            <i className="fas fa-times fa-lg"></i>
          </Button>
        </div>
        <Table
          className="align-items-start table-sort mt-3"
          id="invoice-table"
          responsive
        >
          <tbody>
            <tr>
              <td className="text-center" colSpan="5">
                <h1 className="text-center text-primary">INVOICE</h1>
              </td>
              <td className="text-left" />
            </tr>
            <tr>
              <td className="text-left">
                <h4 className="text-muted">From:</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{providerFullName}</h4>
              </td>
              <td className="text-left"></td>
              <td className="text-right">
                <h4 className="text-md-right">Status</h4>
              </td>
              <td className="text-left">
                <h4 className={`${statusInfo.textColor}`}>
                  {statusInfo.status}
                </h4>
              </td>
              <td className="text-right"></td>
            </tr>
            <tr>
              <td className="text-left">
                <h4 className="text-muted">To:</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{clientFullName}</h4>
              </td>
              <td className="text-left"></td>
              <td className="text-right border-right">
                <h4 className="text-md-right">Invoice</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">#{invoiceNumber}</h4>
              </td>
            </tr>
            <tr>
              <td className="text-left"></td>
              <td className="text-left">
                <h4 className="text-primary">{clientInfo.address || ""}</h4>
              </td>
              <td className="text-left"></td>
              <td className="text-right border-right">
                <h4 className="text-md-right">Issue Date</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{getDateFormat(createdAt)}</h4>
              </td>
            </tr>
            <tr>
              <td colSpan={6}></td>
            </tr>
            <tr>
              <td colSpan={6}></td>
            </tr>
            <tr>
              <td className="text-left border-right">
                <h4 className="text-md-right">Client</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{clientFullName}</h4>
              </td>
              <td className="text-left"></td>
              <td className="text-right border-right">
                <h4 className="text-md-right">Provider</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{providerFullName}</h4>
              </td>
            </tr>
            <tr>
              <td className="text-left border-right">
                <h4 className="text-md-right">Phone</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{clientInfo.phoneNumber}</h4>
              </td>
              <td className="text-left"></td>
              <td className="text-right border-right">
                <h4 className="text-md-right">Email</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{providerInfo.email}</h4>
              </td>
            </tr>
            <tr>
              <td className="text-left border-right">
                <h4 className="text-md-right">Email</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{clientInfo.email}</h4>
              </td>
              <td className="text-left"></td>

              {/* <td className="text-right border-right">
                <h4 className="text-md-right">Tax ID.</h4>
              </td>
              <td className="text-left">
                <h4 className="text-danger">N/A</h4>
              </td> */}

              <td className="text-right border-right">
                <h4 className="text-md-right">Phone</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{providerInfo.phoneNumber}</h4>
              </td>
            </tr>
            {/* <tr>
              <td className="text-left border-right"></td>
              <td className="text-left"></td>
              <td className="text-left"></td>
              <td className="text-right border-right">
                <h4 className="text-md-right">NPI.</h4>
              </td>
              <td className="text-left">
                <h4 className="text-danger">N/A</h4>
              </td>
            </tr> */}
            {/* <tr>
              <td className="text-left border-right"></td>
              <td className="text-left"></td>
              <td className="text-left"></td>
              <td className="text-right border-right">
                <h4 className="text-md-right">Phone</h4>
              </td>
              <td className="text-left">
                <h4 className="text-primary">{providerInfo.phoneNumber}</h4>
              </td>
            </tr> */}
          </tbody>
        </Table>
        <CardFooter>
          <Table
            className="table-sort align-items-center"
            id="total-invoice-table"
            responsive
          >
            <thead>
              <tr style={{ backgroundColor: "bisque" }}>
                <th>Date</th>
                <th>Description</th>
                <th className="text-right">Amount</th>
              </tr>
            </thead>
            <tbody>
              <tr style={{ backgroundColor: "aliceblue" }}>
                <td>{getDateFormat(createdAt)}</td>
                <td>
                  <h4>{description}</h4>
                </td>
                <td className="text-right">
                  <b>${servicePrice}</b>
                </td>
              </tr>
            </tbody>
          </Table>
        </CardFooter>
        <div className="text-center mb-3">
          <Button color="info" onClick={exportPDF}>
            <i className="fas fa-download fa-lg" /> Download PDF
          </Button>
        </div>
      </Container>
    </Modal>
  );
}
