import React, { Component } from 'react';
import { connect } from 'react-redux'
import {
    Button,
    Modal,
    Card,
    CardBody,
} from "reactstrap";
import {
    CardNumberElement,
    CardExpiryElement,
    CardCVCElement,
    Elements,
    injectStripe,
} from 'react-stripe-elements';
import { reloadData } from '../../../redux/actions/clientInvoiceAction'
import clientPaymentApi from '../../../api/clientPaymentApi';
import { toast } from 'react-toastify';
import './PaymentModal.scss';

const createOptions = (fontSize, padding) => {
    return {
        style: {
            base: {
                fontSize,
                color: '#424770',
                letterSpacing: '0.025em',
                fontFamily: 'Source Code Pro, monospace',
                '::placeholder': {
                    color: '#aab7c4',
                },
            },
            invalid: {
                color: '#9e2146',
            },
        },
    };
};

class _SplitForm extends Component {

    handleSubmit = (ev) => {
        ev.preventDefault();
        const { invoiceId } = this.props
        if (this.props.stripe) {
            this.props.stripe
                .createToken()
                .then((payload) => {
                    if (payload.error) {
                        toast.error(payload.error.message);
                    }
                    else {
                        const cardToken = payload.token.id
                        clientPaymentApi.add(cardToken, invoiceId).then(() => {
                            toast.success("Add Payment success !");
                            this.props.reloadData(invoiceId)
                            this.props.toggle()
                        }).catch(err => {
                            toast.error(err.message);
                        })
                    }
                });
        } else {
            console.log("Stripe.js hasn't loaded yet.");
        }
    };
    render() {
        return (
            <Card>
                <CardBody>
                    <form onSubmit={this.handleSubmit}>
                        <div>
                            <b>Card number</b>
                            <CardNumberElement
                                {...createOptions(this.props.fontSize)}
                            />
                        </div>
                        <div>
                            <b>Expiration date</b>
                            <CardExpiryElement
                                {...createOptions(this.props.fontSize)}
                            />
                        </div>
                        <div>
                            <b>CVC</b>
                            <CardCVCElement
                                {...createOptions(this.props.fontSize)}
                            />
                        </div>
                        <div className="text-right"><Button color="success">Add</Button></div>
                    </form>
                </CardBody>
            </Card>
        );
    }
}
const SplitForm = injectStripe(_SplitForm);

const PaymentModal = (props) => {

    const { isOpen, toggle, data } = props;
    const elementFontSize = window.innerWidth < 450 ? '14px' : '18px';
    return (
        <Modal
            className="modal-dialog-centered payment-modal"
            isOpen={isOpen}
            toggle={toggle}
        >
            <div className="modal-header text-center">
                <h6 className="modal-title" id="modal-title-default">
                    Payment Modal
            </h6>
                <button
                    aria-label="Close"
                    className="close"
                    data-dismiss="modal"
                    type="button"
                    onClick={toggle}
                >
                    <span aria-hidden={true}>×</span>
                </button>
            </div>
            <div className="modal-body text-center">
                <div><b >Charge: ${data.priceRemain}</b></div>
                <div><b>Invoice Number: #{data.invoiceNumber}</b></div>
            </div>
            {
                <Elements>
                    <SplitForm fontSize={elementFontSize}
                        invoiceId={data.id}
                        reloadData={props.reloadData}
                        toggle={toggle}
                    />
                </Elements>
            }
        </Modal>
    );
}

const mapActionToProps = { reloadData }

export default connect(null, mapActionToProps)(PaymentModal)
