import jsPDF from 'jspdf';
import 'jspdf-autotable';
import * as moment from 'moment';
import { getDateFormat, getUserFullName } from 'helpers/UtilsHelper';

export const exportClientInvoicePDF = (firstTableId, secondTableId) => {
    const doc = new jsPDF('l', 'pt');
    const highLight = {
        fillColor: [41, 128, 186],
        textColor: [255, 255, 255],
        fontStyle: "bold"
    }
    const pageSize = doc.internal.pageSize;
    const pageWidth = pageSize.width ? pageSize.width : pageSize.getWidth();
    let TITLE = "Billing Invoice";
    doc.text(TITLE, pageWidth / 2, 30, 'center');

    doc.autoTable({
        html: `#${firstTableId}`,
        startY: 55,
        didParseCell: data => {
            if (data.row.index === 0) {
                data.cell.styles = { ...data.cell.styles, ...highLight }
            }
        },
    });
    doc.autoTable({
        html: `#${secondTableId}`,
        // startY: doc.autoTable.previous.finalY + 50,
        // pageBreak: 'always',
        didParseCell: data => {
            if (data.row.index === 1) {
                data.cell.styles = { ...data.cell.styles, ...highLight }
            }
        },
    });
    doc.save(`invoice_${String(moment().valueOf())}.pdf`);
}

export const exportClientBillingPDF = (data, totalCharged, firstTableId, secondTableId) => {
    const fullName = getUserFullName(data.provider)
    const startWeek = moment(data.bookings && data.bookings[0].createdAt).startOf('week');
    const endWeek = moment(data.bookings && data.bookings[0].createdAt).endOf('week');

    const doc = new jsPDF('l', 'pt');
    const highLight = {
        fillColor: [227, 136, 91],
        textColor: [255, 255, 255],
        fontStyle: "bold"
    }
    const cancellation = {
        textColor: [245, 54, 92],
    }
    const pageSize = doc.internal.pageSize;
    const pageWidth = pageSize.width ? pageSize.width : pageSize.getWidth();

    let ETHERA_PAY = "EtheraPay";
    let INVOICE_NUMBER = `Invoice ${data.invoiceNumber}`;
    let PROVIDER_NAME = fullName;
    let DURATION = `${getDateFormat(startWeek)} - ${getDateFormat(endWeek)}`;
    let TOTAL_AMOUNT_DUE = "TOTAL AMOUNT DUE: " + String(totalCharged) + "$"

    doc.text(ETHERA_PAY, pageWidth / 2, 30, 'center');
    doc.text(INVOICE_NUMBER, pageWidth / 2, 55, 'center');
    doc.text(PROVIDER_NAME, pageWidth / 2, 80, 'center');
    doc.text(DURATION, pageWidth / 2, 105, 'center');

    doc.autoTable({
        html: `#${firstTableId}`,
        startY: 120,
        didParseCell: data => {
            var rows = data.table.body;
            if (data.row.index === rows.length - 1) {
                data.cell.styles = { ...data.cell.styles, ...highLight }
            }
            if (data.section === "body" && data.row.raw.className && data.row.raw.className.includes("custom-row__cancellation")) {
                data.cell.styles = { ...data.cell.styles, ...cancellation }
            }
        },
    });
    doc.text(TOTAL_AMOUNT_DUE, pageWidth / 2, doc.autoTable.previous.finalY + 30, 'center');

    doc.autoTable({
        html: `#${secondTableId}`,
        startY: doc.autoTable.previous.finalY + 50,
        pageBreak: 'always',
        didParseCell: data => {
            var rows = data.table.body;
            if (data.row.index === rows.length - 1) {
                data.cell.styles = { ...data.cell.styles, ...highLight }
            }
        },
    });
    doc.save(`Ethera-Pay_${String(moment().valueOf())}.pdf`);
}